html, body {
  margin: 0;
  padding: 0;
  background-color: #000000;
  color: #d1d5db;
  height: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 1.5rem;
  min-height: 100vh;
  background-color: #121212;
  color: #d1d5db;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  color: #6366f1;
}

.layout {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

.sidebar {
  background-color: #1f1f1f;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 0 15px rgb(99 102 241 / 0.5);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-height: none;
  overflow-y: visible;
  width: 100%;
  margin: 0 auto;
}

.sidebarTitle {
  font-size: 1.5rem;
  font-weight: 600;
  color: #818cf8;
  margin-bottom: 1rem;
}

.planButton {
  background-color: #374151;
  color: #d1d5db;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  text-align: left;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.planButton:hover {
  background-color: #6366f1;
  color: white;
}

.planButtonActive {
  background-color: #4f46e5;
  color: white;
  box-shadow: 0 0 10px #818cf8;
}

.noPlans {
  font-style: italic;
  color: #9ca3af;
}

.newPlanForm {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.input {
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: #2a2a2a;
  border: 1.5px solid #6366f1;
  color: #d1d5db;
  font-size: 1rem;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 5px #818cf8;
  border-color: #818cf8;
}

.buttonPrimary {
  background-color: #4f46e5;
  color: white;
  padding: 0.75rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.buttonPrimary:hover {
  background-color: #4338ca;
}

.buttonSecondary {
  background-color: #374151;
  color: #d1d5db;
  padding: 0.75rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.buttonSecondary:hover {
  background-color: #4b5563;
}

.mainPanel {
  background-color: #1f1f1f;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 0 15px rgb(99 102 241 / 0.5);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
}

.planTitle {
  font-size: 2rem;
  font-weight: 600;
  color: #818cf8;
  margin-bottom: 1rem;
}

.daySection {
  background-color: #2a2a2a;
  padding: 1rem;
  border-radius: 0.75rem;
  box-shadow: inset 0 0 8px rgb(99 102 241 / 0.3);
}

.dayTitle {
  font-size: 1.25rem;
  font-weight: 600;
  color: #818cf8;
  margin-bottom: 0.75rem;
  border-bottom: 1px solid #6366f1;
  padding-bottom: 0.3rem;
}

.exerciseList {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.exerciseItem {
  background-color: #1f1f1f;
  padding: 0.75rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 6px rgb(99 102 241 / 0.3);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #d1d5db;
}

.exerciseInfo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.exerciseName {
  font-weight: 600;
  white-space: nowrap;
}

.exerciseDetails {
  font-size: 1.2rem;
  color: #9ca3af;
  white-space: nowrap;
}

.exerciseLink {
  color: #818cf8;
  text-decoration: none;
  font-weight: 500;
  white-space: nowrap;
}

.exerciseLink:hover {
  text-decoration: underline;
}

.deleteButton {
  background: none;
  border: none;
  color: #f87171;
  font-weight: 600;
  cursor: pointer;
  transition: color 0.3s ease;
}

.deleteButton:hover {
  color: #ef4444;
}

.addExerciseButton {
  margin-top: 1.5rem;
  width: 100%;
  padding: 0.75rem;
  background-color: #4f46e5;
  color: white;
  font-weight: 600;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: 0 0 10px #818cf8;
}

.addExerciseButton:hover {
  background-color: #4338ca;
}

.modalOverlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0,0,0,0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  z-index: 50;
}

.modalContainer {
  background-color: #1a1a1a;
  border-radius: 1.25rem;
  padding: 2rem;
  width: 100%;
  max-width: 20rem;
  box-sizing: border-box;
  box-shadow: 0 0 20px rgb(99 102 241 / 0.7);
  color: #d1d5db;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  margin: 0 auto;
}

.modalTitle {
  font-size: 1.5rem;
  font-weight: 600;
  color: #818cf8;
}

.modalSelect, .modalInput {
  width: 100%;
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: #2a2a2a;
  border: 1.5px solid #6366f1;
  color: #d1d5db;
  font-size: 1rem;
}

.modalSelect:focus, .modalInput:focus {
  outline: none;
  box-shadow: 0 0 5px #818cf8;
  border-color: #818cf8;
}

.modalButtons {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}

.modalButtonPrimary {
  background-color: #4f46e5;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.modalButtonPrimary:hover {
  background-color: #4338ca;
}

.modalButtonSecondary {
  background-color: #374151;
  color: #d1d5db;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.modalButtonSecondary:hover {
  background-color: #4b5563;
}

/* Media Queries dla responsywno≈õci */
@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }

  .title {
    font-size: 2rem;
  }

  .layout {
    grid-template-columns: 1fr;
  }

  .sidebar {
    padding: 1rem;
    border-radius: 0.5rem;
  }

  .sidebarTitle {
    font-size: 1.25rem;
  }

  .planButton {
    padding: 0.5rem 0.75rem;
    font-size: 0.9rem;
  }

  .planTitle {
    font-size: 1.5rem;
  }

  .dayTitle {
    font-size: 1rem;
  }

  .exerciseItem {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .exerciseInfo {
    gap: 0.75rem;
  }

  .modalContainer {
    max-width: 90%;
    padding: 1.5rem;
  }

  .modalTitle {
    font-size: 1.25rem;
  }

  .modalSelect, .modalInput {
    font-size: 0.9rem;
    padding: 0.5rem;
  }
}

@media (max-width: 480px) {
  .title {
    font-size: 1.5rem;
  }

  .sidebarTitle {
    font-size: 1rem;
  }

  .planButton {
    font-size: 0.85rem;
  }

  .daySection {
    padding: 0.75rem;
  }

  .exerciseDetails {
    font-size: 1rem;
  }

  .deleteButton {
    font-size: 1.25rem;
  }
}